import moment, { months } from 'moment';

/**
 * StringUtil
 * @author deokchanjung
 */
const formatter = {
  date: (value, format) => moment(value).format(format),
  ymd: value =>
    `${moment(value).year()} ${moment(value).month() + 1} ${moment(value).date()}`,
  day: value => {
    switch (moment(value).day()) {
      case 0: {
        return '';
      }
      case 1: {
        return '';
      }
      case 2: {
        return '';
      }
      case 3: {
        return '';
      }
      case 4: {
        return '';
      }
      case 5: {
        return '';
      }
      case 6: {
        return '';
      }
      default: {
        return '';
      }
    }
  },
};

export default { formatter };
