import TokenService from '../TokenService';
import RestService from '../RestService';

/**
 * 
 * @param {*} payload (username: string), (password: string)
 * @returns
 */
const signIn = async payload => {
  const url = '/auth/sign-in';
  const response = await RestService.post(url, payload);

  if (response.accessToken) {
    TokenService.setAccessToken(response.accessToken);
  }
  return response;
};

/**
 * 
 */
const signOut = async () => {
  const url = '/auth/sign-out';
  const response = await RestService.post(url);
  TokenService.removeAccessToken();
  return response;
};

/**
 *   
 * @param {*} password 
 */
const checkPassword = async password => {
  const url = '/auth/password/check';
  const response = await RestService.post(url, {
    password,
  });
  return response;
};

/**
 *   
 */
const getAccountInfo = async () => {
  const url = '/auth/donor/account';
  const response = await RestService.get(url);
  return response;
};

/**
 *   
 * @param {*} id   
 */
const updateAccountTerms = async id => {
  const url = `/feedback/donor/account/${id}/terms`;
  const response = await RestService.put(url);
  return response;
};

/**
 *    
 * @param {*} payload
 *    - {string} password 
 *    - {string} name 
 *    - {string} rank 
 *    - {string} position 
 *    - {string} mobile 
 *    - {string} email 
 * @returns
 */
const updateAccountInfo = async payload => {
  const url = '/auth/donor/account';
  const response = await RestService.put(url, payload);
  return response;
};

export default {
  signIn,
  signOut,
  checkPassword,
  getAccountInfo,
  updateAccountTerms,
  updateAccountInfo,
};
