/* eslint-disable max-len */
/**
 *   
 */
export const REGEX_NUMBER = /[^0-9]/g;

/**
 *  
 */
export const REGEX_SPACE = /\s/g;

/**
 *  
 */
export const REGEX_LINE_BREAK = /(?:\r\n|\r|\n)/g;

/**
 *   
 */
export const REGEX_NUMBER_COMMA = /\B(?=(\d{3})+(?!\d))/g;

/**
 * 10     
 */
export const REGEX_SIX_HUNDRED_THOUSAND = /^\d{6}$/g;

/**
 *      
 */
export const REGEX_HUNDREDS_PLACE_VALUE = /(^\d+$)|(^\d+\.\d{0,2}$)/g;

/**
 *        
 */
export const REGEX_IGNORE_DUPLICATE_DOTS = /^\d*[.]\d*$/g;

/**
 * Email 
 */
export const REGEX_EMAIL =
  /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

/**
 * '-'  
 */
export const REGEX_MOBILE = /^\d{3}\d{3,4}\d{4}$/;

/**
 *     
 */
export const REGEX_PHONE = /(^02.{0}|^01.{1}|[0-9]{3})([0-9]+)([0-9]{4})/;

/**
 * URL 
 */
export const REGEX_URL = /(http(s)?:\/\/)?([a-z0-9\w]+\.*)+[a-z0-9]{2,4}/gi;

/**
 * PASSWORD
 */
//   1 : ,   6~15
export const REGEX_PASSWORD_1 = /^(?=.*[a-zA-Z])(?=.*[0-9]).{6,15}$/;

//   2 : ,   6~15
export const REGEX_PASSWORD_2 = /^(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9]).{6,15}$/;

//   3 : , ,   6~15
export const REGEX_PASSWORD_3 = /^(?=.*[^a-zA-Z0-9])(?=.*[0-9]).{6,15}$/;
