import Axios from 'axios';
import HttpStatus from 'http-status';
import env from '../environments';
import TokenService from './TokenService';

const RestService = {
  get: null,
  post: null,
  put: null,
  delete: null,
  request: null,
};

const axiosConfig = {
  baseURL: env.API_URL,
  validateStatus: status => status < 400,
};

const axiosInstance = Axios.create(axiosConfig);

/**
 *       
 * @param response   
 */
const responseSuccessHandler = response => {
  const { data } = response;
  return data || response;
};

const responseErrorHandler = error => {
  const { response } = error;

  if (!response) {
    return new Promise((resolve, reject) => {
      reject(error.toJSON());
    });
  }

  if (response.data) {
    return new Promise((resolve, reject) => {
      reject({
        ...response.data,
        status: error.config.status,
      });
    });
  }

  if (response.status === HttpStatus.UNAUTHORIZED) {
    return new Promise((resolve, reject) => {
      reject(response);
    });
  }
  return response;
};

axiosInstance.interceptors.response.use(responseSuccessHandler, responseErrorHandler);

/**
 *    
 * @param isSecureReqest  
 * @param customConfig    
 */
const getRequestConfig = (isSecureRequest, customConfig) => {
  let config = {};

  try {
    if (customConfig) {
      config = Object.assign(config, customConfig);
    }

    if (isSecureRequest) {
      const accessToken = TokenService.getAccessToken();

      if (accessToken) {
        config.headers = {
          Authorization: `Bearer ${accessToken}`,
        };
      }
    }
    return config;
  } catch (error) {
    return config;
  }
};

/**
 * GET 
 * @param url   
 * @param payload   (HTTP Body)
 * @param isSecureReqest  
 */
RestService.get = (url, payload = null, isSecureRequest = true) => {
  const config = getRequestConfig(isSecureRequest, {
    data: payload,
  });
  return axiosInstance.get(url, config);
};

/**
 * POST 
 * @param url   
 * @param payload   (HTTP Body)
 * @param isSecureReqest  
 */
RestService.post = (url, payload = null, isSecureReqest = true) => {
  const config = getRequestConfig(isSecureReqest);
  return axiosInstance.post(url, payload, config);
};

/**
 * PUT 
 * @param url   
 * @param payload   (HTTP Body)
 * @param isSecureReqest  
 */
RestService.put = (url, payload = null, isSecureReqest = true) => {
  const config = getRequestConfig(isSecureReqest);
  return axiosInstance.put(url, payload, config);
};

/**
 * DELETE 
 * @param url   
 * @param isSecureReqest  
 */
RestService.delete = (url, isSecureReqest = true) => {
  const config = getRequestConfig(isSecureReqest);
  return axiosInstance.delete(url, config);
};

/**
 *    , RestService 
 * get/post/put/delete      
 * @param config 
 */
RestService.request = config => {
  return axiosInstance.request(config);
};

export default RestService;
