import qs from 'querystring';
import RestService from '../RestService';

/**
 *   
 * @param {*} conditions  (start: number),   (length: number)
 * @returns
 */
const getNotificationList = async conditions => {
  const url = `/auth/notification?${qs.stringify(conditions)}`;
  const response = await RestService.get(url);
  return response;
};

const readAlarm = async id => {
  const url = `/auth/notification/${id}`;
  await RestService.put(url);
};

export default { getNotificationList, readAlarm };
